@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
/*Poppins*/

/*Global*/
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Poppins', sans-serif;
}

/*Main Sign up Page*/
.sign-up{
  display: flex;
}
.welcome-area{
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  align-items: center;
  background-color: #1F618D;
  color: white;
  font-size: 1.4rem;
}
.welcome-area i{
  font-size: 100px;
}
.sign-up-area{
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.sign-up-area input {
  margin-left: 25px;
  margin-top: 20px;
  padding: 10px 40px 10px 10px;
  transition: 0.3s;
  border: solid 1px;
  border-radius: 10px;
}
.sign-up-area input:focus {
  padding: 10px 5px 10px 10px;
}
.btn{
  display: flex;
  justify-content: center;
}
.submit-btn{
  padding: 10px 20px;
  background-color: #1F618D;
  color: white;
  cursor: pointer;
  border: none;
  margin-top: 20px;
  font-size: 16px;
  border-radius: 10px;
  transition: 0.3s;
}
.submit-btn:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container {
    width: 90%;
    margin: 0 auto;
}
/*Header*/
header {
    display: flex;
    justify-content: space-between;
    height: 70px;
    align-items: center;
}
.header-container {
    background-color: #1F618D;
}
.logo-container {
    display: flex;
    align-items: center;
    color: white;
}
.logo-container i {
    font-size: 60px;
}
.nav-links {
    display: flex;
    list-style: none;
}
nav a{
    text-decoration: none;
    color: white;
    margin-left: 55px;
    font-size: 20px;
}
.burger{
    display: none;
}
.burger div {
    height: 3px;
    width: 30px;
    background-color: white;
    margin-top: 3px;
}

/*Main area*/
.main {
    display: flex;
    justify-content: space-between;
}
.main-sidebar {
    width: 30%;
}
.main-feed {
    width: 40%;
    color: red;
}
.recommended {
    width: 30%;
}
.sidebar {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 90vh;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}
.sidebar-component {
    display: flex;
    align-items: center;
    width: 50%;
    padding: 20px;
    transition: 0.3s;
}
.sidebar-component:hover {
    background-color: #1F618D;
    color: white;
    border-radius: 20px;
    cursor: pointer;
}
.sidebar-component h1 {
    margin-left: 10px;
    font-size: 23px;
}
.sidebar-component i {
    font-size: 30px;
}
